import { useState } from 'react';
import { ModalContainer } from './smModalStyled';
import { BsPlusSquareFill, BsDashSquare } from 'react-icons/bs';
import 'react-phone-number-input/style.css';
import PhoneInput from 'react-phone-number-input';
function Modal({ open, data, handleModalClose }) {
	console.log(data);
	const [title, setTitle] = useState('');
	const [URLs, setURLS] = useState([]);
	const [URLinput, setURLinput] = useState('');
	const [mobileNum, setMobileNum] = useState('');
	const [email, setEmail] = useState('');
	const [bankAccountNum, setBankAccountNum] = useState('');
	return (
		<ModalContainer>
			<div className={open ? 'openModal modal' : 'modal'}>
				{open ? (
					<section>
						<header> </header>
						<main className={'sm-main'}>
							<section>
								<div></div>
								<div>
									<input
										type="text"
										value={title}
										onChange={(e) => {
											setTitle(e.target.value);
										}}
									></input>
								</div>
							</section>
							<section>
								<div> </div>
								<div className="video-url">
									<span>Youtube URL:</span>
									<ul>
										{/* show inputs */}
										{URLs.map((el, idx) => {
											return (
												<li key={idx}>
													<input value={el} readOnly></input>
													<BsDashSquare
														onClick={() => {
															let copyArr = URLs.slice();
															copyArr.splice(idx, 1);
															setURLS(copyArr);
														}}
													/>
												</li>
											);
										})}
									</ul>
									{/* default input box */}
									<input
										onChange={(e) => {
											setURLinput(e.target.value);
										}}
										value={URLinput}
									></input>
									<BsPlusSquareFill
										onClick={() => {
											if (URLinput) {
												//input 
												let copyArr = URLs.slice();
												copyArr.push(URLinput);
												setURLS(copyArr);
												setURLinput('');
											}
										}}
									/>
								</div>
							</section>
							<section>
								<div></div>
								<div>
									<PhoneInput
										placeholder="   "
										onChange={setMobileNum}
										value={mobileNum}
									/>
									<button className="auth-btn"></button>
									{mobileNum}
								</div>
							</section>
							<section>
								<div></div>
								<div>
									<input placeholder="   "></input>
									<button> </button>
								</div>
							</section>
							<section>
								<div></div>
								<div>
									<select name="account_bank_id">
										<option value=""> </option>
										<option value="35"></option>
										<option value="29"></option>
										<option value="7"></option>
										<option value="5"></option>
										<option value="15"></option>
										<option value="17"></option>
										<option value="25"></option>
										<option value="47"></option>
										<option value="27"></option>
										<option value="3"></option>
										<option value="41"></option>
										<option value="37"></option>
										<option value="11"></option>
										<option value="36"></option>
										<option value="60"></option>
										<option value="39"></option>
										<option value="9"></option>
										<option value="19"></option>
										<option value="56"></option>
										<option value="33"></option>
										<option value="31"></option>
										<option value="68"></option>
										<option value="67"></option>
										<option value="59"></option>
										<option value="23"></option>
										<option value="45">HSBC</option>
										<option value="21">SC</option>
									</select>
									<input
										type="number"
										value={bankAccountNum}
										onChange={(e) => {
											setBankAccountNum(e.target.value);
										}}
									></input>
								</div>
							</section>
							<section>
								<div></div>
								<div>  </div>
							</section>
							<section>
								<div></div>
								<div>
									<input></input>
								</div>
							</section>
							<section>
								<div></div>
								<div>
									<input></input>
								</div>
							</section>
						</main>
						<footer>
							<button
								className="close"
								onClick={() => {
									handleModalClose('submission');
								}}
							>
								{' '}
								{' '}
							</button>
						</footer>
					</section>
				) : null}
			</div>
		</ModalContainer>
	);
}
export default Modal;
