import { useState } from 'react';
import { ModalContainer } from './SubmitModalStyled';
// ReactModal.setAppElement('#root');
function SubmitModal({ open, data, handleModalClose }) {
	console.log('data: ', data);
	console.log('open: ', open);
	return (
		<ModalContainer>
			<div className={open ? 'openModal modal' : 'modal'}>
				{open ? (
					<section>
						<header> </header>
						<main>
							<section>
								<span className="cate"></span>
								<span className="submit-value">sample url</span>
							</section>
							<section>
								<span className="cate"></span>
								<span className="submit-value"></span>
								<span className="authorized"></span>
							</section>
							<section>
								<span className="cate"></span>
								<span className="submit-value">010-0000-0000</span>
								<span className="authorized">
									<kbd></kbd>
								</span>
							</section>
							<section>
								<span className="cate"></span>
								<span className="submit-value">xxxx@xxxx.com</span>
								<span className="authorized">
									<kbd></kbd>
								</span>
							</section>
							<section>
								<span className="cate"></span>
								<span className="submit-value"> | 000-000-0000</span>
								<span className="authorized">
									<kbd></kbd>
								</span>
							</section>
							<section>
								<span className="cate"> </span>
								<span className="submit-value"></span>
							</section>
							<section>
								<span className="cate"></span>
								<span className="submit-value">test map.ppt</span>
							</section>
							<section>
								<span className="cate"> </span>
								<span className="submit-value"></span>
							</section>
						</main>
						<footer>
							<button
								className="close"
								onClick={() => {
									handleModalClose();
								}}
							>
								{' '}
								{' '}
							</button>
							<button className="return"></button>
							<button className="feedback"></button>
							<button className="okay"></button>
						</footer>
					</section>
				) : null}
			</div>
		</ModalContainer>
	);
}
export default SubmitModal;
